<template>
  <div>
    <div v-if="!submitted">
      <form
        action=""
        method="post"
        id="_frmForm"
        name="frmForm"
        @submit.prevent="addEmployee"
      >
        <table class="content_table">
          <colgroup>
            <col style="width: 30%;" />
            <col style="width: 70%;" />
          </colgroup>
          <tr>
            <th></th>
            <td>
              <!--  -->
              {{article.subject}}
            </td>
          </tr>
          <tr>
            <th></th>
            <td>
              <!--  -->
              {{article.writer}}
            </td>
          </tr>
          <tr style="height: 100px">
            <th></th>
            <td>
              <!--  -->
              {{article.content}}
            </td>
          </tr>
          <tr></tr>
          <tr>
            <th></th>
            {{article.createDate}}
            <td>
              <!--  -->
            </td>
          </tr>
         
        </table>
      <button class="btn btn-success" v-on:click="goList">
        
      </button>
      <button class="btn btn-success" v-on:click="deleted">
        
      </button>
      <button class="btn btn-success" v-on:click="modifyed">
        
      </button>
      </form>
    </div>

  </div>
</template>

<script>
import http from "../http-common4";
export default {
  name: 'QuestionItem',
  data() {
    return {
      idx : 0,
      article:[]
    };
  },
  props: ["id"],
  methods: {
    selectApt: function(apt) {
      this.$emit('select-apt', apt);
    },
    retrieveArticles() {
      http
        .get("/findArticleById/"+this.idx)
        .then(response => (this.article = response.data))
        .catch(() => {
          this.errored = true;
        })
        .finally(() => (this.loading = false));
    },
    deleted(){
      http
        .post("/deleteArticle/" + this.idx)
        .then(response => {
          if (response.data.state == "succ") {
            alert(" .");
            this.$router.push("/question");
          } else {
            alert("  .");
          }
        })
        .catch(() => {
          this.errored = true;
        })
        .finally(() => (this.loading = false));
    },
    modifyed(){
      this.$router.push("/question/modify/"+this.idx)
    },
    goList(){
        this.$router.push("/question")
    }
  },
  mounted() {
    this.idx = this.id;
    this.retrieveArticles();
  }
};
</script>

<style scoped>
.img-list {
  width: 50px;
}
.mouse-over-bgcolor {
  background-color: lightblue;
}
</style>
