<template>
  <div>
    <div v-if="!submitted">
      <form
        action=""
        method="post"
        id="_frmForm"
        name="frmForm"
        @submit.prevent="addArticle"
      >
        <table class="content_table">
          <colgroup>
            <col style="width: 30%;" />
            <col style="width: 70%;" />
          </colgroup>
          <tr>
            <th></th>
            <td>
              <input
                data-msg=""
                type="text"
                name="name"
                id="_name"
                v-model=getProfile
                style="width: 30%;"
                disabled
              />
            </td>
          </tr>
          <tr>
            <th></th>
            <td>
              <input
                data-msg=""
                type="text"
                name="subject"
                id="subject"
                size="20"
                v-model="subject"
                style="width: 30%;"
              />
            </td>
          </tr>
          <tr>
            <th></th>
            <td>
              <textarea
                data-msg=""
                name="content"
                id="content"
                v-model="content"
                style="width: 30%; height:100px"
              />
            </td>
          </tr>
          <tr></tr>
          
          <tr>
            <td colspan="2" style="height: 50px; text-align: center;">
              <button type="submit" name="button"></button>
            </td>
          </tr>
        </table>
      </form>
    </div>

    <div v-else>
      <h4>  !</h4>
      <button class="btn btn-success" v-on:click="newArticle">
          
      </button>
      <button class="btn btn-success" v-on:click="goList">
        
      </button>
    </div>
  </div>
</template>

<script>
import http from '../http-common4'
import { mapGetters, mapState } from 'vuex'
export default {
  name: 'add-customer',
  data() {
    return {
      cname: '',
      subject: '',
      content: '',
      submitted : false
    }
  },
  methods: {
    addArticle() {
      if (this.subject == '') {
        alert(' ')
        return
      }
      if (this.content == '') {
        alert(' ')
        return
      }

      http
        .post('/addArticle', {
          subject: this.subject,
          writer: this.getProfile,
          content: this.content,
        })
        .then((response) => {
          if (response.data.state == 'succ') {
            alert(' .')
          } else {
            alert('  ..')
          }
        })
      this.submitted = true
    },
    newArticle() {
      this.cname =  '';
      this.subject = '';
      this.content = '';
      this.submitted = false;
      this.$router.push("/question/add")
    },
    goList(){
        this.$router.push("/question")
    }
  },
  computed: {
      ...mapGetters(['isAuthenticated', 'isProfileLoaded','getProfile']),
      ...mapState({
        authLoading: state => state.auth.status === 'loading'
       // ,uname: state => `${state.user.getProfile}`,
      }),
    // loading: function () {
    //   return this.authStatus === 'loading' && !this.isAuthenticated
    // }
  }
}
</script>

<style>
.submitform {
  max-width: 300px;
  margin: auto;
}
</style>
