<template>
  <div>
    <div v-if="!submitted">
      <form
        action=""
        method="post"
        id="_frmForm"
        name="frmForm"
        @submit.prevent="modifyArticle"
      >
        <table class="content_table">
          <colgroup>
            <col style="width: 30%;" />
            <col style="width: 70%;" />
          </colgroup>
          <tr>
            <th></th>
            <td>
              <input
                data-msg=""
                type="text"
                name="name"
                id="_name"
                v-model=getProfile
                style="width: 30%;"
                disabled
              />
            </td>
          </tr>
          <tr>
            <th></th>
            <td>
              <input
                data-msg=""
                type="text"
                name="subject"
                id="subject"
                size="20"
                v-model="subject"
                style="width: 30%;"
              />
            </td>
          </tr>
          <tr>
            <th></th>
            <td>
              <textarea
                data-msg=""
                name="content"
                id="content"
                v-model="content"
                style="width: 30%; height:100px"
              />
            </td>
          </tr>
          <tr></tr>
          
          <tr>
            <td colspan="2" style="height: 50px; text-align: center;">
              <button type="submit" name="button"></button>
            </td>
          </tr>
        </table>
      </form>
    </div>

    <div v-else>
      <h4>  !</h4>
      <button class="btn btn-success" v-on:click="goArticle">
        
      </button>
    </div>
  </div>
</template>

<script>
import http from '../http-common4'
import { mapGetters, mapState } from 'vuex'
export default {
  name: 'add-customer',
  data() {
    return {
      idx:0,
      subject:"",
      content:"",
      submitted : false
    }
  },
  props: ["id"],
  methods: {
      retrieveArticles() {
      http
        .get("/findArticleById/"+this.idx)
        .then(response => (
            this.subject = response.data.subject,
            this.content = response.data.content
            )
            
            )
        .catch(() => {
          this.errored = true;
        })
        .finally(() => (this.loading = false));
    },
    modifyArticle() {
      if (this.subject == '') {
        alert(' ')
        return
      }
      if (this.content == '') {
        alert(' ')
        return
      }

      http
        .post('/updateArticle/', {
            idx:this.idx,
          subject: this.subject,
          writer: this.getProfile,
          content: this.content,
        })
        .then((response) => {
          if (response.data.state == 'succ') {
            alert(' .')
          } else {
            alert('  ..')
          }
        })
      this.submitted = true
    },
    goArticle(){
        this.$router.push("/question/view/"+this.idx)
    }
  },
  computed: {
      ...mapGetters(['isAuthenticated', 'isProfileLoaded','getProfile']),
      ...mapState({
        authLoading: state => state.auth.status === 'loading'
       // ,uname: state => `${state.user.getProfile}`,
      }),
    // loading: function () {
    //   return this.authStatus === 'loading' && !this.isAuthenticated
    // }
  },
  mounted() {
    this.idx = this.id;
    this.retrieveArticles();
  }
}
</script>

<style>
.submitform {
  max-width: 300px;
  margin: auto;
}
</style>
